.CreateForm {
    padding: 30px 0;
    background-color: #eee;
}

.form_container {
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
}

.form_container input {
    width: 70%;
    font-size: 20px;
    height: 40px;
    float: left;
    border: 0;
    border-bottom: 1px solid #ddd;
    border-radius: 15px;
}

.form_container input:focus {
    outline: none;
}

.form_container button {
    border-radius: 20px;
    width: 13%;
    float: right;
    height: 45px;
    font-size: 15px;
}
